import React, { useState } from 'react'
import axios from 'axios'
import icon from "./icon.png"
import ShowTemp from './ShowTemp'
function App() {
    const [city, setCity] = useState("")
    const [data, setData] = useState({
        description: "",
        temp: 0,
        temp_max: 0,
        temp_min: 0,
        humidity: 0,
        sunrise: 0,
        sunset: 0,
        country: "",
    })

    const handleClick = () => {
        axios.get(`http://api.openweathermap.org/data/2.5/weather?q=${city}&appid=c412e32f8374f6a87ce341d095a159f6`)
            .then((response) => {
                setData({
                    description: response.data.weather[0].description,
                    temp: response.data.main.temp,
                    temp_max: response.data.main.temp_max,
                    temp_min: response.data.main.temp_min,
                    humidity: response.data.main.humidity,
                    sunrise: response.data.sys.sunrise,
                    sunset: response.data.sys.sunset,
                    country: response.data.sys.country,
                })
            })
    }

    return (
        <>
            <div id="weather" className=' text-center '>
                <div class="icon"> <img src={icon}></img>
                    <h1 >Weather</h1>
                </div>
                <div class="searchbutton">
                    <input placeholder="Enter your city.." type="text" className='search' value={city} onChange={(e) => {
                        setCity(e.target.value);
                    }} />
                    <button className='btn btn-primary mx-1' type='submit' onClick={handleClick}>submit</button>
                </div>
            </div>

            <ShowTemp text={data}></ShowTemp>
        </>
    )
}

export default App
